.skills-container {
    height: 100vh;
    width: 100vw;
    display: flex;
    text-align: center;
    align-items: center;
    align-content: center;
    gap: 1%;
}
.skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 80vh;
    width: 50vw;
}
.skill {
    display: flex;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
    height: auto;
    width: auto;
    margin: 5px;
    background: white;
    box-shadow: 10px 15px 7px -6px rgba(0, 0, 0, 0.301);
}
.skill:hover {
    transform: scale(1.2);
}
.progress {
    margin: auto;
    text-align: center;
    height: 100px;
    width: 100px;
}
.text {
    margin-left: 100px;
    width: 50vw;
    text-align: start;
    margin-right: 50px;
}
.text h1 {
    background: var(--red);
    font-size: 2.2rem;
    font-weight: 800;
    padding: 5px 20px;
}
.skill h6 {
    margin-right: 10px;
}

@media screen and (max-width : 420px) {
    .skills-container {
        height: auto;
        width: 100vw;
        display: flex;
        flex-direction: column;
        margin-bottom: 150px;
    }
    .skills {
        display: flex;
        flex-wrap: wrap;
        width: auto;
        text-align: center;
    }
    .skill {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        padding: 20px;
    }
    .text {
        margin-left: 0;
        width: 90vw;
        text-align: start;
        margin-right: 0;
    }
    .text h1 {
        font-size: 20pt;
        color: var(--red);
        background: none;
        margin: 0;
    }
}