.navbar {
    z-index: 50;
    position: fixed;
    height: 100vh;
    min-width: 25pt;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 0 10px 10px 0;
  }
  .link-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .link {
    padding: 15px;
    color: white;
    display: flex;
    align-items: center;
    border-radius: 10px;
    margin: 5px;
    text-decoration: none;
    background: var(--light-gray);
  }
  .link:hover {
    background: var(--light);
    color: #FF4C29;
  }
  .fa-icon {
    font-size: 25px;
  }
  .link span {
    font-size: 12pt;
    margin-left: 10pt;
    position: relative;
  }
  .divider {
    height: 1px;
    width: 100%;
    background:  var(--light);
    margin: 20px;
  }
  .visible {
    display: block !important;
  }
  
  .dark-mode-btn {
    font-size: 1.3rem;
    color: white;
    margin-bottom: 40px;
    border: 1.5px solid white;
    border-radius: 50%;
    padding: 2px;
    background: var(--dark-gray);
  }

  @media screen and (max-width : 420px) {
    .navbar {
      background: var(--black);
      bottom: 0;
      flex-direction: row;
      height: 80px;
      width: 100vw;
      border-radius: 0;
      justify-content: center;
    }
    .link-container {
      flex-direction: row;
    }
    .link {
      flex-direction: column;
      padding: 20px;
    }
    .divider, .logo, .dark-mode, .link span  {
      display: none;
    }
  }