:root {
    --blue : #38a5ff;
}
.portfolio-container {
    display: flex;
    height: 100vh;
    width: auto;
    justify-items: space-around;
}
.profile {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.profile-card {
    margin: auto;
    background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
    height: 500px;
    width: 300px;
    padding: 20px;
    box-shadow: 5px 10px 10px 5px rgba(0,0,0,0.1);
}
.profile img {
    height: 120px;
    width: 120px;
    border-radius: 50%
}
.biography {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.adress {
    display: flex;
    gap: 10%;
}
.a-left {
    color: var(--light);
    font-weight: 500;
}

.buttonClass {
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#ffffff;
    border-color:#124d77;
    border-top-left-radius:3px;
    border-top-right-radius:3px;
    border-bottom-left-radius:3px;
    border-bottom-right-radius:3px;
    box-shadow:inset 0px 1px 0px 0px #54a3f7;
    text-shadow:inset 0px 1px 0px #154682;
    background:linear-gradient(#007dc1, #0061a7);
  }
  
  .buttonClass:hover {
    background: linear-gradient(#0061a7, #007dc1);
  }

  @media screen and (max-width : 480px) {
    .portfolio-container {
        display: flex;
        flex-direction: column;
        height: auto;
    }
    .biography, .profile {
        margin-top: 20pt;
    }
  }